
import {obs, install, on, map, bundle} from '../node_modules/thinsite/exports.js';


let content = bundle
    `<h1>Hello, World!</h1>
    ${map([1,2,3], (n, i, arr) => bundle
        `<div>
            <button ${on.click(ev => arr.splice(i, 1))}></button>
            ${n}
        </div>`
    )}`;

install(content, document.body);